/**
 * 
 *
 * baseUrl: 
 * routerMode: 
 * imgBaseUrl: 
 *
 */
let baseUrl;
let routerMode;
const imgBaseUrl = 'http://localhost:3000';

if (process.env.NODE_ENV == 'development') {
	baseUrl = 'http://localhost:3000';
	routerMode = 'hash'
}else{
	baseUrl = '';
	routerMode = 'hash'
}

export {
	baseUrl,
	routerMode,
	imgBaseUrl
}



